
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;



@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.memorandum-detail {
  position: relative;
  padding: 15px 20px;
  background: #fff;
  overflow-y: auto !important;

  .tool-bar {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
  }

  .icon-left {
    font-size: 22px;
    cursor: pointer;
  }

  .title {
    font-size: 36px;
    color: #262626;
    border-bottom: 1px solid #e6e6e6;
  }

  .edit {
    font-size: 16px;
    color: inherit;
  }
}
