
// 主题颜色
$theme-color: #20ABFE;

// 边框灰色
$border-gray-color: #ebedf0;



@mixin flex-center($x: center, $y: center) {
  display: flex;
  justify-content: $x;
  align-items: $y;
}

@mixin lineClamp($line: 3) {
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: $line;
  /* autoprefixer: off */
  -webkit-box-orient: vertical;
}

@mixin ellipsis() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

$bg-color: #304156;
.sidebar {
  background: $bg-color;
  user-select: none;

  .ant-menu-dark {
    background: $bg-color;
  }

  .ant-menu-sub {
    background: $bg-color !important;
    box-shadow: none !important;
  }

  .sider-menu-logo {
    overflow: hidden;
    height: 64px;
    line-height: 64px;
    background: #2b2f3a;
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    text-align: center;
  }
}